import anime from 'animejs';
import './styles.css';

const popUpAnimation = () => {
    anime({
        targets: '#pop-up',
        translateY: "1rem",
        duration: 1500,
        easing: 'easeOutElastic',
        complete: oopsAnimation
    });
    /*
    gsap.from( "#pop-up", {
        y: -100,
        ease: 'elastic.out(1, 0.3)',
        duration: 2,
        onComplete: oopsAnimation
    });
    */
}

const oopsAnimation = () => {
    anime({
        targets: '.test .el',
        translateY: "-0.3rem",
        direction: 'alternate',
        duration: 500,
        delay: anime.stagger(250),
        easing: 'easeInOutSine',
        loop: true,
    });
}

window.onload = () => {
    popUpAnimation();
}